.map {
  width: 957px;
  min-height: 530px;
  margin: 0 auto 38px auto;
  background-image: url("../../img/map.png");

  @media (max-width: $tablet-width) {
    width: 698px;
    min-height: 545px;
    background-image: url("../../img/map-tablet.png");
  }

  @media (max-width: $mobile-width) {
    width: 320px;
    min-height: 250px;
    background-image: url("../../img/map-mobile.png");
  }
}
